.panel {
}
.panelHeader {
  @apply flex mt-12 items-center text-2xl font-medium;
}
.panelHeader i {
  @apply text-3xl font-medium;
}
.panelTitle {
  @apply mt-16 text-3xl font-medium;
}
.panelLinks {
  @apply w-full text-xl flex flex-col gap-6 font-bold opacity-50 mt-8;
}
.panelLink {
  @apply flex justify-between h-9;
}
.panelLink i {
  @apply text-3xl font-medium;
}
