.slider {
  display: flex;
  overflow-x: scroll;
  gap: 100px;
  scroll-behavior: smooth;
}
.buttons {
}
.button {
  transition: 0.3s;
}
.item {
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
}
.art {
  width: 100%;
  height: 700px;
}
.art img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: 0 0;
}
.text {
  @apply text-xl mt-4;
}

.slider::-webkit-scrollbar {
  width: 10px; /* в основном для вертикальных полос прокрутки */
  height: 10px; /* в основном для горизонтальных полос прокрутки */
}
.slider::-webkit-scrollbar-thumb {
  /* плашка-бегунок */
  border-radius: 100px;
  @apply bg-lightGreen;
}
.slider::-webkit-scrollbar-track {
  /* фон */
  @apply bg-transparent;
}
@media (min-width: 320px) {
  .slider {
    gap: 20px;
  }
  .item {
    flex: 0 0 85%;
    padding-bottom: 10px;
  }
  .art {
    width: 100%;
    height: 95vw;
  }
  .buttons {
    display: none;
  }
  .slider::-webkit-scrollbar {
    display: none;
  }
  .slider::-webkit-scrollbar-thumb {
    /* плашка-бегунок */
    display: none;
  }
  .slider::-webkit-scrollbar-track {
    /* фон */
    display: none;
  }
}
@media (min-width: 576px) {
  .slider {
    gap: 15px;
  }
  .item {
    flex: 0 0 40%;
    padding-bottom: 10px;
  }
  .art {
    width: 100%;
    height: 70vw;
  }
  .buttons {
    display: none;
  }
  .slider::-webkit-scrollbar {
    display: none;
  }
  .slider::-webkit-scrollbar-thumb {
    /* плашка-бегунок */
    display: none;
  }
  .slider::-webkit-scrollbar-track {
    /* фон */
    display: none;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .slider {
    gap: 37px;
  }

  .item {
    flex: 0 0 40%;
  }
  .art {
    width: 100%;
    height: 50vw;
  }
  .art img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 0 0;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .slider {
    gap: 45px;
  }
  .buttons {
    display: block;
  }
  .item {
    flex: 0 0 30%;
  }
  .art {
    width: 100%;
    height: 400px;
  }
  .art img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 0px 0;
  }
}

/* // X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .slider {
    gap: 55px;
  }

  .item {
    flex: 0 0 30%;
  }
  .art {
    width: 100%;
    height: 570px;
  }
  .art img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: -30px 0;
  }
}

/* // XX-Large devices (larger desktops, 1400px and up) */
@media (min-width: 1440px) {
  .slider {
    gap: 70px;
  }

  .item {
    flex: 0 0 30%;
  }
  .art {
    width: 100%;
    height: 570px;
  }
  .art img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 0 0;
  }
}
@media (min-width: 1700px) {
  .slider {
    gap: 80px;
  }

  .item {
    flex: 0 0 30%;
  }
  .art {
    width: 100%;
    height: 700px;
  }
  .art img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 0 0;
  }
}
